main.opportunities-page {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 20px;
	padding: 20px;
}

section.opportunities {
	width: min(95%, 600px);
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 10px;
}

.loading-message {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
	font-size: 20px;
	margin-top: 2rem;
}

.job-listing-container {
	width: 100%;
	display: flex;
	flex-direction: column;
}

.job-listing-container > button {
	margin: 20px 0 20px 0;
}

.job-listing {
	border: 2px solid var(--pink);
	background-color: white;
	margin: 10px 0;
	border-radius: 20px;
	padding: 10px;
	text-align: center;
}

.job-listing > a {
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 5px;
	align-items: center;
	text-decoration: none;
	color: var(--dark-purple);
}

.opportunities-title {
	text-align: center;
	border-radius: 40px;
	background-color: white;
	padding: 15px;
	width: min(90%, 600px);
	margin-top: 4rem;
}

.search {
	width: min(90%, 600px);
	display: flex;
}

.search-bar {
	display: flex;
	justify-content: center;
	width: 100%;
	margin: auto;
	gap: 10px;
}

.search-input {
	outline: none;
	border-color: lightgray;
	border-radius: 15px;
	border-width: 1.75px;
	padding: 0 10px;
	font-family: "Baloo 2";
	font-weight: 500;
	font-size: 18px;
	width: 100%;
}

.opportunities-page button {
	background-color: var(--dark-blue);
	color: white;
	border: none;
	padding: 5px 25px;
	border-radius: 15px;
	font-size: 18px;
	font-family: "Baloo 2";
	text-transform: uppercase;
	cursor: pointer;
}

.opportunities-page button:hover {
	background-color: #1b4f6d;
}

@media screen and (max-width: 850px) {
	main.opportunities-page {
		margin: 0;
		padding: 0;
	}
}
